<template>
    <nav class="nav flex-row-between">
        <img src="~@/assets/images/common/logo.png" class="nav-logo"/>
        <div class="nav-list flex-1 flex-row-start hidden-xs-only">
            <div 
                class="flex-row-center nav-list__item pointer" 
                v-for="(item, index) in navList" 
                :key="item.name"
                @click="togglePage(item.path)">
                <img :src="item.icon" class="nav-list__icon" />
                <span class="nav-list__text" :class="{'active': index == 0}">{{item.name}}</span>
            </div>
        </div>
        <div class="nav-right flex-row-end">
            <div 
                class="nav-login text-center pointer text-16" 
                @click="connectHandler" 
                v-loading="connectLoading"
                v-if="!getAccount">
                Connect
            </div>
            <el-dropdown v-else>
                <div 
                    class="nav-login text-center pointer text-16 flex-row-center" >
                    {{getAccount}}
                    <el-icon color="#fff"><caret-bottom /></el-icon>
                </div>
                <template #dropdown>
                    <el-dropdown-menu>
                        <el-dropdown-item @click="logout">sign out</el-dropdown-item>
                    </el-dropdown-menu>
                </template>
            </el-dropdown>
            <el-dropdown trigger="click">
                <div class="flex-row-center nav-group">
                    English
                    <el-icon color="#fff">
                        <caret-bottom />
                    </el-icon>
                </div>
                <template #dropdown>
                    <el-dropdown-menu>
                        <el-dropdown-item class="nav-group__list flex-row-start">
                            <span class="text-white">English</span>
                        </el-dropdown-item>
                        <el-dropdown-item class="nav-group__list flex-row-start">
                            <span>简体中文</span>
                        </el-dropdown-item>
                    </el-dropdown-menu>
                </template>
            </el-dropdown>
        </div>
    </nav>
</template>

<script>
import { CaretBottom } from '@element-plus/icons'
import { ElNotification } from 'element-plus'
export default {
    components: {
        CaretBottom
    },
    data() {
        return {
            navList: [{
                name: 'Dashboard',
                icon: require('@/assets/images/common/dashboard.png'),
                path: '/Dashboard'
            },{
                name: 'Gem',
                icon: require('@/assets/images/common/gem.png'),
                path: '/Gem'
            },{
                name: 'NFT',
                icon: require('@/assets/images/common/nft.png'),
                path: '/NFT'
            },{
                name: 'GameFi',
                icon: require('@/assets/images/common/gamefi.png'),
                path: '/GameFi'
            },{
                name: 'More',
                icon: require('@/assets/images/common/more.png'),
                path: '/More'
            }],
            account: '',
            connectLoading: false,
        }
    },
    computed: {
        getAccount() {
            if (this.account) {
                return '0x....'+ this.account.substr(-4)
            }
            return ''
        }
    },
    mounted(){
        this.account = this.$wallet.accounts[0]
        this.$wallet.on('accountChange', () => {
            this.account = this.$wallet.accounts[0]
        })
    },
    methods: {
        logout() {
            this.$wallet.logout()
        },
        async connectHandler() {
            this.connectLoading = true
            try {
                await this.$wallet.connect()
                this.connectLoading = false
            } catch (error) {
                this.connectLoading = false
            }
            
        },
        /**
         * 跳转到指定页面
         * @param {String} path 页面路由地址
         */
        togglePage(path) {
            ElNotification({
                title: 'Coming soon',
                message: 'Coming soon',
            })
            return;
            if (!path) {
                return
            }
            this.$router.push({
                path
            })
        }
    }
}
</script>

<style lang="scss">
    .el-dropdown-menu{
        background: #1B1B1B !important;
    }
    .el-popper.is-light{
        border: none !important;
    }
    .nav-group__list{
        align-items: center !important;
    }
    .nav-group__list-icon{
        height: .24rem;
        margin-right: .2rem;
    }
    .el-dropdown__popper.el-popper[role=tooltip]{
        border-color: #8F8F8F !important;
    }
    .el-dropdown-menu__item:focus, .el-dropdown-menu__item:not(.is-disabled):hover{
        background: #86713D !important;
        color: #FCBF2F !important;
        .nav-list__text{
            color: #FCBF2F !important;
        }
    }
</style>
<style lang="scss" scoped>
.nav-list__text.active{
    color: #FCBF2F;
}
.nav{
    height: .8rem;
    padding: 0 .3rem;
    align-items: center;
    background: #181818;
    position: relative;
    &-logo{
        width: .55rem;
        height: .55rem;
    }
    &-list{
        padding-left: .6rem;
    }
    &-list__item{
        margin-right: .4rem;
        &.select{
            align-items: center;
            margin-right: 0;
        }
    }
    &-list__icon{
        width: .2rem;
        height: .2rem;
        margin-right: .1rem;
    }
    &-list__arrow{
        width: .1rem;
        height: auto;
        margin-left: .1rem;
    }
    &-list__text{
        font-size: .16rem;
        color: #fff;
    }
    .nav-login{
        padding: .1rem .2rem;
        background: linear-gradient(-85deg, #EB9202, #F1B90E);
        border-radius: .1rem;
        color: #fff;
        margin-right: .15rem;
    }
    .nav-group{
        color: #fff;
    }

    
}
</style>